import React, { Component } from 'react'

import Conecta from './Conecta'
import ItemLista from './ItemLista'

import './Listagem.css'

export default class Listagem extends Component {

  state = {
    ofertas: []
  }

  async componentDidMount() {
    // verifica se há conexão com a internet
    if (navigator.onLine) {      
      const lista = await Conecta.get('8cb534beba36766c9be051fefdccc335/teste/teste')
      this.setState({ofertas: lista.data.teste})
      
      // salva em localStorage os dados para posterior acesso (quando 
      // estiver sem conexão)
      localStorage.setItem('super_noite', JSON.stringify(lista.data))
    } else {
      // se não tiver conexão, obtém a lista anteriormente salva em localStorage
      const lista = await JSON.parse(localStorage.getItem('super_noite'))
      this.setState({ofertas: lista.teste})
    }
  }

  render() {
    return (
      <div className="container mt-2">
        <div className="row">
          {this.state.ofertas.map(oferta => (
            <ItemLista key={oferta.nome}
               nome={oferta.nome}
               marca={oferta.marca}
               preco={oferta.preco}
               foto={oferta.foto} />
          ))}
        </div>        
      </div>
    )
  }
}
