import React, { Component } from 'react'

export default class Header extends Component {
  render() {
    return (
      <div className="container-fluid bg-dark text-secondary py-3">
        <div className="container">
          <h2>New Satolep Times</h2>
          <h3><b>Notícias do Dia</b></h3>
          <h5 className = "text-white"><i>Noticias do primeiro semestre do ano de 2020</i></h5>
          
        </div>
      </div>
    )
  }
}
